body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.scrollbar {
  margin-left: 30px;
  float: left;
  height: 300px;
  background: #fff;
  overflow-y: scroll;
  margin-bottom: 25px;
}






.scrollbar-near-moon {
scrollbar-color: #b490ca #f5f5f5;
}

.scrollbar-near-moon::-webkit-scrollbar-track {
-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
background-color: #F5F5F5;
border-radius: 10px; }

  .scrollbar-near-moon::-webkit-scrollbar {
      width: 12px;
      background-color: #F5F5F5; }

.scrollbar-near-moon::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
      background-image: linear-gradient(to top, #5ee7df 0%, #b490ca 100%); }
      
.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

.base-container {
  background: url(/static/media/strikers_background.2b298bfe.png) no-repeat;
  background-size: 100% 100%;
  opacity: 0.8;
  height: calc(100vh - 100px)   !important

}


.about-container {
  position: relative;
  display: flex;
  text-align: center;
  flex-direction: column;
  color: black;
  text-shadow: 0 0 5px #fff;
  background-color: #adb5bd;
  background: linear-gradient(170deg, #adb5bd 70%, black 30%)
}
  
.information-container {
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: center;
  padding-top: 100px;
}

.sms-column {
  padding-right: 300px;
  font-size: 32px;
  display: flex;
  flex-direction: column;
  
}

.msc-column {
  font-size: 32px;
}



.home-header {
  font-family: Arvo,sans-serif;
}
.home-container {
  height: 100vh;
  width: 100%
}

.home {
  overflow: scroll;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}
::-webkit-scrollbar {
  width: 0;  /* Remove scrollbar space */
  background: transparent;  /* Optional: just make scrollbar invisible */
}

.partners-container {
  background: linear-gradient(170deg, black 70%, #adb5bd 30%);

  color: white;
  position: relative;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
}

.partnerBubble {
  justify-content: center;
  display: flex;
  align-items: center;
      
  position: absolute;
  padding-top: 50px;
  
  margin: auto;
}

.home-button {
  border-radius: 12px;
}

.discord-auth {

}

.home-header-container {
  flex-direction: row;
  display: flex;
  justify-content: flex-end;
}
.partnerProfile {
    display: flex;
    flex-direction: column;
    height: 200px;
    width: 200px;
}

.partnerImg {
    height:75%;
    width:75%;
}

.profilePhoto {
    height: 150px;
    justify-content: center;
    display: flex;
    align-items: center;
}

.partnerTitle {
font-family: Arvo,sans-serif;
}

.partnerDescription {
    font-family: Robotico, sans-serif
}

.profilePhoto:hover > .partnerImg {
    height: 100%;
    width: 100%;
    transition: all .5s;
}


.ranking {
    height: 2000px;
}

.ranking-card {
    width:700px;
    
    box-shadow:0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%)
    
}

.ranking-card-container {
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
}

.profile-body {
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-header {
    width:100%;
}

.clickable-header {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.megastriker {
    color: orange !important;
}

.legend {
    color: purple !important;
}

.superstar {
    color: #4da6ff !important;
}

.professional {
    color:#cccc00 !important;
}

.rookie {
    color: green !important;
}

.tourn-photo {
    height:600px;
    width:100%;
    margin:auto;
    display:flex;
    cursor: pointer;
}

.carousel-container {
    border: .2rem solid #ececec;
    border-top-color: rgb(236, 236, 236);
    border-top-style: solid;
    border-top-width: 0.2rem;
    border-right-color: rgb(236, 236, 236);
    border-right-style: solid;
    border-right-width: 0.2rem;
    border-bottom-color: rgb(236, 236, 236);
    border-bottom-style: solid;
    border-bottom-width: 0.2rem;
    border-left-color: rgb(236, 236, 236);
    border-left-style: solid;
    border-left-width: 0.2rem;
    border-image-source: initial;
    border-image-slice: initial;
    border-image-width: initial;
    border-image-outset: initial;
    border-image-repeat: initial;
    width: 60%;
    margin:auto;
        
}

.tourney-container {
    display: flex;
    justify-content: center;
    align-content: center;
    text-align: center;
    flex-direction: column;
}

.tourney-content {
    text-align: center;
    padding-right: 200px;
}

.crumble-subheader {
    text-decoration: underline;
    font-weight: bold;
    text-align: center;
}

.crumble-interview {
    text-align: left;
    padding-bottom: 20px;
}

.tourney-results {
    padding-right:100px;
}


@media (max-width: 768px) {
    .crumble-interview {
            text-align: left;
     } 
    
     .tourney-content {
         padding-right: 0
     }
  }
.rules-container {
    width: 100%;
    height: 800px;
}
